@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@100;200&family=Frank+Ruhl+Libre&family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amiri&family=David+Libre&family=Quicksand:wght@300&display=swap);
h1,
h2,
h3,
h4,
p,
span,
a {
  color: #e7e7e7;
  font-family: 'Quicksand', sans-serif;
  /* font-size: 1rem !important; */
  letter-spacing: 0;
  background-color: rgba(0, 0, 0, 0.4);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
h1,
h2,
h3,
h4 {
  font-family: 'Frank Ruhl Libre', serif;
  /* font-size: 1.2rem !important; */
  letter-spacing: normal;
}

div {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

html,
body {
  margin: 0;
  height: 100%;
  text-align: center;
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  letter-spacing: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Quicksand', sans-serif;
}

img {
  vertical-align: middle;
}

#tsparticles canvas {
  position: fixed;
  z-index: -1;
}

.section {
  padding-top: 22vh;
  margin-bottom: 22vh;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  /* width: max-content; */
  /* max-width: 85vw; */
  width: 90vw;
}

/* The sticky class is added to the navbar with JS when it reaches its scroll position */
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}
@media screen and (max-width: 600px) {
  .section {
    padding-top: 0;
    margin-bottom: 15vh;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    width: 90vw;
  }
}

.topnav {
  text-align: center;
  padding: 9px;
  position: fixed !important;
  z-index: 99 !important;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  left: 0;
  top: 0;
  width: 100%;
  align-items: center;
  align-self: center;
  background-color: rgba(0, 0, 0, 0.775);
}
.topnav a.active {
  color: rgb(79, 255, 73) !important;
}
.topnav a:hover {
  color: #15cdfc !important;
}
.topnav svg {
  display: none;
}
.topnav a {
  float: left;
  display: block;
  letter-spacing: normal;
  text-align: center;
  text-decoration: none;
  color: #e7e7e7 !important;
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .topnav {
    justify-content: flex-start;
    align-items: flex-start;
  }
  .topnav a {
    display: none;
  }
  /* .topnav a.active {
    display: block;
  } */
  .topnav svg {
    display: flex;
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive a {
    display: flex;
    font-size: 1.6rem;
  }

  .topnav.responsive {
    flex-direction: column;
  }
}

#tsparticles {
  width: 0px;
}



.footer {
  text-align: center;
  padding: 9px;
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  left: 0;
  bottom: 0;
  /* height: 40px; */
  width: 100%;
  background-color: rgba(0, 0, 0, 0.775);
}
.footer a {
  display: flex;
  letter-spacing: normal;
  font-size: large;
  color: #e7e7e7;
}
