.avatar-box {
  display: flex;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
.avatar {
  width: 10vw;
  min-width: 150px;
  border-radius: 50%;
  margin-right: 30px;
}
body {
  background: rgb(38, 37, 48);
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.home-container {
  margin-left: auto;
  margin-right: auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  max-width: 100vw;
}
#dust {
  position: absolute;
  width: 100%;
  top: 0;
}
.typingeffect span {
  color: rgb(79, 255, 73) !important;
  background-color: rgba(0, 0, 0, 0.6);
  font-size: x-large !important;
}
h1 {
  /* font-size: 2em; */
  margin-top: 50px;
  opacity: 1;
  position: relative;
  color: #e3e3eb;
  font-weight: 400;
  text-align: center;
  margin-right: 10px;
  padding-bottom: 10px;
  display: inline-block;
  border-bottom: 3px solid #e3e3eb;
  letter-spacing: 10px;

  > div {
    position: relative;
    display: inline-block;
    + div {
      display: block;
    }
  }
  span {
    letter-spacing: normal;
    display: block;
    margin-bottom: 10px;
    font-size: 0.5em;
    font-style: italic;
    position: relative;
    z-index: 2;
    &:before,
    &:after {
      content: '';
      width: 43%;
      height: 3px;
      border-radius: 5px;
      background: #e3e3eb;
      position: absolute;
      right: 0;
      z-index: 1;
      top: 0.5em;
    }
    &:after {
      left: 0;
    }
  }
}
p {
  color: #fff;
  margin-top: 14px;
  letter-spacing: 5px;
  line-height: 1.2em;
}

/*ANimation stuff*/
.title {
  -webkit-animation-name: zoomIn;
          animation-name: zoomIn;
  -webkit-animation-duration: 1.75s;
          animation-duration: 1.75s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* padding-top: 5%; */
}

@-webkit-keyframes zoomIn {
  0% {
    transform: scale(0.3);
  }
  40% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes zoomIn {
  0% {
    transform: scale(0.3);
  }
  40% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 600px) {
  .title {
    padding-top: 17vh;
  }
}

